@import "https://fonts.googleapis.com/css?family=Open+Sans:300,400&display=swap";
body {
  color: #d1d5db;
  background-color: #111827;
  margin: 0;
  padding: 0;
  font-family: Open Sans, sans-serif;
}

.Main {
  grid-template-columns: minmax(300px, 868px);
  justify-content: center;
  padding: 10px;
  display: grid;
}

.Items {
  grid-gap: 1.5rem;
  grid-row-gap: 1.5em;
  grid-template-columns: repeat(3, 2fr);
  display: grid;
}

.Card {
  background-color: #4b5563;
  border-radius: 5px;
  margin: 0 0 20px;
  text-decoration: none;
  animation-name: fade;
  animation-duration: 4s;
  display: block;
  box-shadow: 8px 14px 38px #ffffff0f, 1px 3px 8px #ffffff08;
}

.Card img {
  width: 100%;
  height: 80%;
  border-radius: 5px 5px 0 0;
}

.container-body {
  width: 100%;
  height: 20%;
  display: flex;
}

.Card h2 {
  color: #d1d5db;
  width: 70%;
  align-self: center;
  padding: 5px 0 5px 10px;
  font-size: 18px;
  font-weight: 300;
}

a {
  color: #d1d5db;
  width: 25%;
  height: 32px;
  text-align: center;
  border: 2px solid #111827;
  border-radius: 20px;
  align-self: center;
  margin-right: 10px;
  padding-top: 10px;
  text-decoration: none;
}

a:hover {
  color: #f0f8ff;
  background-color: #111827;
}

@keyframes fade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

